.searchBox {
  width: fit-content;
  height: fit-content;
  position: relative;
}
.inputSearch {
  height: 50px;
  width: 50px;
  border-style: none;
  padding: 10px;

  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  transition: all 0.5s ease-in-out;
  background-color: #22a6b3;
  padding-right: 40px;
  color: #fff;
}
.inputSearch::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
}
.btnSearch {
  z-index: 5;
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  left: 0px;
  color: #ffffff;
  //   background-color: transparent;
  pointer-events: painted;
}
.btnSearch:focus ~ .inputSearch {
  width: 300px;
  border-radius: 0px;
  //   transform: translate(-250px);
  //   background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}
.inputSearch:focus {
  width: 300px;
  border-radius: 0px;
  //   transform: translate(-250px);
  //   background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}
